import React from 'react';
import { Box, Typography } from '@mui/material';

export default function SectionHeader({ title, subtitle, sx = {} }) {
  return (
    <Box sx={{ mb: 3, ...sx }}>
      <Typography
        variant="h2"
        sx={{
          fontWeight: 900,
          letterSpacing: 0.5,
          fontSize: { xs: '2rem', sm: '2.4rem', md: '2.8rem' },
          background: (t) => t.palette.mode === 'dark'
            ? 'linear-gradient(90deg, #e3b04b, #90caf9)'
            : 'linear-gradient(90deg, #f6c56b, #b2d7e4)',
          WebkitBackgroundClip: 'text',
          WebkitTextFillColor: 'transparent',
        }}
      >
        {title}
      </Typography>
      {subtitle && (
        <Typography sx={{ color: (t) => t.palette.text.secondary, mt: 0.5, fontSize: { xs: '1.05rem', sm: '1.15rem' } }}>
          {subtitle}
        </Typography>
      )}
    </Box>
  );
}
