import React, { useState, useEffect } from 'react';
import { Box, Typography, Paper, Chip, Stack, Tooltip } from '@mui/material';
import ScienceIcon from '@mui/icons-material/Science';
import BiotechIcon from '@mui/icons-material/Biotech';
import TerminalIcon from '@mui/icons-material/Terminal';
import InfoOutlinedIcon from '@mui/icons-material/InfoOutlined';

function TypingTitle({ onDone }) {
  const [display, setDisplay] = useState('');
  const [phase, setPhase] = useState('tgp');

  useEffect(() => {
    let timer;
    if (phase === 'tgp') {
      const tgp = 'TGP';
      if (display.length < tgp.length) {
        timer = setTimeout(() => setDisplay(tgp.slice(0, display.length + 1)), 70);
      } else {
        timer = setTimeout(() => setPhase('backslash'), 400);
      }
    } else if (phase === 'backslash') {
      timer = setTimeout(() => setPhase('erase'), 220);
    } else if (phase === 'erase') {
      if (display.length > 0) {
        timer = setTimeout(() => setDisplay(display.slice(0, -1)), 40);
      } else {
        timer = setTimeout(() => setPhase('full'), 120);
      }
    } else if (phase === 'full') {
      const full = 'Tree Guided Primers';
      if (display.length < full.length) {
        timer = setTimeout(() => setDisplay(full.slice(0, display.length + 1)), 40);
      } else {
        timer = setTimeout(() => onDone(), 250);
      }
    }
    return () => clearTimeout(timer);
  }, [display, phase, onDone]);

  return (
  <Typography
      variant="h1"
      sx={{
        fontWeight: 900,
        background: (t) => t.palette.mode === 'dark'
          ? 'linear-gradient(90deg, #e3b04b, #ffab91, #90caf9)'
          : 'linear-gradient(90deg, #f6c56b, #ffb199, #cfe8f1)',
        WebkitBackgroundClip: 'text',
        WebkitTextFillColor: 'transparent',
  fontSize: 'clamp(2.6rem, 8.5vw, 9.2rem)',
  lineHeight: 1.08,
        textShadow: '0 8px 32px rgba(0,0,0,0.18)',
        userSelect: 'none',
        cursor: 'pointer',
  minHeight: '1em',
        letterSpacing: 3,
  mb: 2.5,
        '@keyframes caretBlink': {
          '0%, 49%': { opacity: 1 },
          '50%, 100%': { opacity: 0 },
        },
      }}
    >
      {display}
      {phase !== 'full' || display.length < 'Tree Guided Primers'.length ? (
        <Box
          component="span"
          sx={{
            display: 'inline-block',
            ml: 0.5,
            fontWeight: 700,
            animation: 'caretBlink 1s steps(1, end) infinite',
            color: (t) => t.palette.primary.main,
            WebkitTextFillColor: (t) => t.palette.primary.main,
            textShadow: 'none',
          }}
        >
          |
        </Box>
      ) : null}
    </Typography>
  );
}

