import React from 'react';
import { Paper } from '@mui/material';

export default function GlassCard({ children, sx = {}, ...rest }) {
  return (
    <Paper
      elevation={0}
      sx={{
        p: { xs: 2.5, sm: 3.5, md: 5 },
        position: 'relative',
        overflow: 'hidden',
        borderRadius: 16,
        // Remove hard border and use soft inner hairline + shadows
        border: '1px solid transparent',
        background: (t) => t.palette.mode === 'dark'
          ? 'linear-gradient(180deg, rgba(18,18,18,0.58) 0%, rgba(18,18,18,0.50) 100%)'
          : 'linear-gradient(180deg, rgba(255,255,255,0.50) 0%, rgba(255,255,255,0.42) 100%)',
        backdropFilter: 'blur(18px) saturate(140%)',
        WebkitBackdropFilter: 'blur(18px) saturate(140%)',
        backgroundClip: 'padding-box',
        boxShadow: (t) => t.palette.mode === 'dark'
          ? '0 10px 26px rgba(0,0,0,0.28), 0 2px 10px rgba(0,0,0,0.18)'
          : '0 10px 28px rgba(0,0,0,0.10), 0 2px 10px rgba(0,0,0,0.06)',
        transition: 'transform 250ms cubic-bezier(.2,.8,.2,1), box-shadow 250ms, background 250ms, backdrop-filter 250ms',
        '&::before': {
          // subtle top highlight for glass effect
          content: '""',
          position: 'absolute',
          inset: 0,
          borderRadius: 'inherit',
          pointerEvents: 'none',
          background: (t) => t.palette.mode === 'dark'
            ? 'linear-gradient(180deg, rgba(255,255,255,0.05), rgba(255,255,255,0))'
            : 'linear-gradient(180deg, rgba(255,255,255,0.22), rgba(255,255,255,0))',
          opacity: 1,
        },
        '&::after': {
          // fade the bottom edge so no border-like rim appears
          content: '""',
          position: 'absolute',
          left: 0,
          right: 0,
          bottom: 0,
          height: 24,
          borderBottomLeftRadius: 'inherit',
          borderBottomRightRadius: 'inherit',
          pointerEvents: 'none',
          background: (t) => t.palette.mode === 'dark'
            ? 'linear-gradient(180deg, rgba(0,0,0,0), rgba(0,0,0,0.08))'
            : 'linear-gradient(180deg, rgba(255,255,255,0), rgba(0,0,0,0.04))',
        },
        '&:hover': {
          transform: 'translateY(-2px)',
          boxShadow: (t) => t.palette.mode === 'dark'
            ? '0 16px 36px rgba(0,0,0,0.32), 0 4px 14px rgba(0,0,0,0.20)'
            : '0 16px 38px rgba(0,0,0,0.12), 0 4px 14px rgba(0,0,0,0.08)'
        },
        ...sx,
      }}
      {...rest}
    >
      {children}
    </Paper>
  );
}
